import { iosVhFix } from "./utils/ios-vh-fix";

import { initModals } from "./modules/modals/init-modals";
import initTabs from "./modules/tabs";
import "./modules/custom-select";
import "./modules/init-form-validate";
import { initMobileMenu } from "./modules/init-mobile-menu";

// ---------------------------------

window.addEventListener("DOMContentLoaded", () => {
  // Utils
  // ---------------------------------

  //   ie11
  // ieFix();
  // https://github.com/fregante/object-fit-images
  // polyfillObjectFit();

  iosVhFix();

  // Modules
  // ---------------------------------

  //       'DOMContentLoaded',     'load'
  //  load   ,      
  window.addEventListener("load", () => {
    initModals();
    initTabs();
    initMobileMenu();
  });

  //   Start
  //        .
  // ToDo    .

  const tabItems = document.querySelectorAll(".tabs__link");
  const tabBlocks = document.querySelectorAll(".tabs__item");

  tabItems.forEach(onTabClick);

  function onTabClick(item) {
    item.addEventListener("click", (event) => {
      event.preventDefault();
      let tabLink = item.getAttribute("href");
      let currentBlock = document.querySelector(tabLink);
      if (!item.classList.contains("_active")) {
        tabItems.forEach((item) => {
          item.classList.remove("_active");
        });
        tabBlocks.forEach((item) => {
          item.classList.remove("_active");
        });
        item.classList.add("_active");
        currentBlock.classList.add("_active");
      }
    });
    document.querySelector(".tabs__link").click();
  }

  //       
  const menuItems = document.querySelectorAll(".account__menu-list-item");
  const menuList = document.querySelector(".account__menu-list");
  const instruction = document.querySelector(".account__instructions");

  if (menuItems && menuList && instruction) {
    menuItems.forEach((item) => {
      if (item.classList.contains("_is-authorized")) {
        instruction.classList.add("_hidden");
        menuList.classList.add("border-none");
      } else {
        instruction.classList.remove("_hidden");
        menuList.classList.remove("border-none");
      }
    });
  }
});

